import { Component, OnInit } from '@angular/core';
import { RecordsService } from '../records.service';

@Component({
  selector: 'app-ec-activity',
  templateUrl: './ec-activity.component.html',
  styleUrls: ['./ec-activity.component.css']
})
export class EcActivityComponent implements OnInit {

  public ec_records = [];

  constructor(private ec_recordservice : RecordsService) { }

  ngOnInit() {
    this.ec_recordservice.getECRecord()
                         .subscribe(data => this.ec_records = data);
  }

}
