import { Component, OnInit } from '@angular/core';
import { RecordsService } from '../records.service';

@Component({
  selector: 'app-achievement-records',
  templateUrl: './achievement-records.component.html',
  styleUrls: ['./achievement-records.component.css']
})
export class AchievementRecordsComponent implements OnInit {

  public ach_records = [];

  constructor( private ach_recordservice : RecordsService ) { }

  ngOnInit() {
    this.ach_recordservice.getAchRecord()
                         .subscribe(data => this.ach_records = data);
  }

}
