import { Component, OnInit } from '@angular/core';
import { RecordsService } from '../records.service';

@Component({
  selector: 'app-contact-records',
  templateUrl: './contact-records.component.html',
  styleUrls: ['./contact-records.component.css']
})
export class ContactRecordsComponent implements OnInit {

  public cont_records = [];

  constructor(private contact_r : RecordsService) { }

  ngOnInit() {
    this.contact_r.getContRecord()
                         .subscribe(data => this.cont_records = data);
  }

}
