import { Component, OnInit } from '@angular/core';
import { RecordsService } from '../records.service';

@Component({
  selector: 'app-cc-activity',
  templateUrl: './cc-activity.component.html',
  styleUrls: ['./cc-activity.component.css']
})
export class CcActivityComponent implements OnInit {

  public cc_records = [];

  constructor(private cc_recordservice : RecordsService) { }

  ngOnInit() {
    this.cc_recordservice.getCCRecored()
                         .subscribe(data => this.cc_records = data);
  }

}
