import { Component, OnInit } from '@angular/core';
import { RecordsService } from '../records.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-project-records',
  templateUrl: './project-records.component.html',
  styleUrls: ['./project-records.component.css']
})
export class ProjectRecordsComponent implements OnInit {

  public pro_records =[];

  constructor(private p_records : RecordsService,private http: HttpClient) { }

  ngOnInit() {
    this.p_records.getProjRecord()
                         .subscribe(data => this.pro_records = data);
  }

}
